<template>
  <div class="pageBox shoppCartBox">
    <div class="shoppCartHeader">
        
      <p>
        
      </p>
    </div>
    <ul class="shoppCart">
      <li v-for="(item,ind) in simulationData.content">
        <div class="after shoppCartListTit">
          <p class="shoppCheck">
            <i class="icon iconfont shoppCheckIcon " v-if="item.check==true" @click="shopCheckFun(item,2)"></i>
            <i class="icon iconfont icon-danxuan"  v-else="item.check==false" @click="shopCheckFun(item,1)"></i>
          </p>
          <a class="after shoppName">
            <i class="icon iconfont icon-iconfontdianpu"></i>
             {{item.name}}
            <i class="icon iconfont icon-bread"></i>
          </a>
          <p class="shoppCoupon" v-show="item.lingjuan">
            
          </p>
        </div>

        <div class="after shoppCartListCont" v-for="(itemTwo,index) in item.lists">
          <div class="after shoppCartListposition "
               @touchstart="touchstartFun"
               @touchmove ="touchmoveFun($event)"
               :data-id="'refItem'+'_'+ind+'_'+index">
            <p class="shoppCheck">
              <i class="icon iconfont shoppCheckIcon " v-if="itemTwo.check==true" @click="commodityCheckFun(itemTwo,item.id)"></i>
              <i class="icon iconfont icon-danxuan"  v-if="itemTwo.check==false" @click="commodityCheckFun(itemTwo,item.id)"></i>
            </p>
            <div class="shoppCartListContImgBox">
              <img :src="itemTwo.src" alt="" class="img-my">
            </div>

            <div class="shoppCartListContFontBox">
              <div class="shoppCartListContFont">
                <p>
                  {{itemTwo.font}}
                </p>
              </div>
              <div class="after">
                <p class="price">
                   {{itemTwo.Tolprice}}
                </p>
                <div class="shoppCalculation">
                  <p class="shoppCalculationIcon reduce" @click="commodityReduce(itemTwo.price,itemTwo.id,item.id,itemTwo.number)">
                    <img src="@/assets/img/reduce.png" alt="" class="img-my">
                  </p>
                  <p class="shoppCalculationIconNumber">
                    {{itemTwo.number}}
                  </p>
                  <p class="shoppCalculationIcon plus" @touchend="commodityPlus(itemTwo.maxNumber,itemTwo.price,itemTwo.id,item.id,itemTwo.number)">
                    <img src="@/assets/img/plus.png" alt="" class="img-my">
                  </p>
                </div>
              </div>
            </div>
            <div class="shoppCartListContRight">
              
            </div>
          </div>


            </div>







      </li>
    </ul>
    <div class="after shoppSettlement">
      <p class="shoppCheck">
        <i class="icon iconfont shoppCheckIcon " v-if="simulationData.check==true" @click="selectAllFun(2)"></i>
        <i class="icon iconfont icon-danxuan"  v-if="simulationData.check==false" @click="selectAllFun(1)"></i>
       <span></span>
      </p>
      <div class="after shoppSettlementCont">
        <p class="totalPrice">
           <span>{{totalPrice}}</span>
        </p>
        <p class="shoppSettlementHerf">
          <a></a>
        </p>
      </div>
      <!--<div class="after shoppSettlementCont">-->
        <!--<p class="shoppSettlementCollection">-->
            <!---->
        <!--</p>-->
        <!--<p class="shoppSettlementHerf">-->
          <!--<a></a>-->
        <!--</p>-->
      <!--</div>-->
    </div>
    <bottom></bottom>
  </div>
</template>
<script>
  import { Toast } from 'mint-ui';
  import bottom from '@/components/bottom'

    export default {
      name:'shoppingCart',
      components: {
        bottom,
      },
      data() {
            return {
              shoppCheck:true,
              className:"is-active",
              totalPrice:'0',//
              touchNumber:'0',//
              startX: 0, //X
              leftX: 0, //x
              simulationData:{//
                code:"1",//
                check:false,
                content:[
                  {
                    id:"1", //ID
                    lingjuan:"1",
                    check:false,
                    name:"HEFWNG",
                    lists:[
                      {
                        id:"22",
                        src:"https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1525682422473&di=994572da18a7223ec47156a40433156a&imgtype=0&src=http%3A%2F%2Fupload.chinaz.com%2F2014%2F1024%2F1414115612300.jpg",
                        font:"  ",
                        price:"10",
                        Tolprice:"50",
                        number:"5",
                        check:false,
                        maxNumber:"10"
                      },
                      {
                        id:"12",
                        src:"https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1525682422473&di=994572da18a7223ec47156a40433156a&imgtype=0&src=http%3A%2F%2Fupload.chinaz.com%2F2014%2F1024%2F1414115612300.jpg",
                        font:"  4545",
                        price:"10",
                        Tolprice:"50",
                        number:"4",
                        check:false,
                        maxNumber:"10"
                      },
                    ],
                  },
                  {
                    id:"2", //ID
                    lingjuan:"1",
                    check:false,
                    name:"HEFWNG",
                    lists:[
                      {
                        id:"23",
                        src:"https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1525682422473&di=994572da18a7223ec47156a40433156a&imgtype=0&src=http%3A%2F%2Fupload.chinaz.com%2F2014%2F1024%2F1414115612300.jpg",
                        font:"  ",
                        price:"10",
                        Tolprice:"20",
                        number:"2",
                        check:false,
                        maxNumber:"10"
                      },
                      {
                        id:"24",
                        src:"https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1525682422473&di=994572da18a7223ec47156a40433156a&imgtype=0&src=http%3A%2F%2Fupload.chinaz.com%2F2014%2F1024%2F1414115612300.jpg",
                        font:"  4545",
                        price:"10",
                        Tolprice:"40",
                        number:"4",
                        check:false,
                        maxNumber:"10"
                      },
                      {
                        id:"26",
                        src:"https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1525682422473&di=994572da18a7223ec47156a40433156a&imgtype=0&src=http%3A%2F%2Fupload.chinaz.com%2F2014%2F1024%2F1414115612300.jpg",
                        font:"  4545",
                        price:"10",
                        Tolprice:"40",
                        number:"4",
                        check:false,
                        maxNumber:"10"
                      },
                    ],
                  },

                ]
              }
            }
        },
        methods: {
        /**
         * 1    
         * @param flag 1--- 2 ----3
         * 1 
         */
          selectAllFun(flag){
            console.log('---------------'+flag);
            if(flag===1){
              //
              this.simulationData.check=true;
              for(let i=0;i<this.simulationData.content.length;i++){
                this.simulationData.content[i].check=true;
                for(let j=0;j<this.simulationData.content[i].lists.length;j++){
                  this.simulationData.content[i].lists[j].check=true
                }
              }
            }else if(flag===2){
              //
              this.simulationData.check=false;
              for(let i=0;i<this.simulationData.content.length;i++){
                this.simulationData.content[i].check=false;
                for(let j=0;j<this.simulationData.content[i].lists.length;j++){
                  this.simulationData.content[i].lists[j].check=false
                }
              }
            }else {
              this.simulationData.check=false;
            }
          /**
           * 
           */
          this.shopTotalFun()
          },
          /**
           *
           * 
           * @param row 
           * @param flag  1   2- 3-
           */
          shopCheckFun(row,flag){
            let slength=0;
            let shopArr=[];
          if(flag===1){
            //
            for(let i=0;i<this.simulationData.content.length;i++){
              if(Number(row.id)===Number(this.simulationData.content[i].id)){
                this.simulationData.content[i].check=true;
                for(let j=0;j<this.simulationData.content[i].lists.length;j++){
                  this.simulationData.content[i].lists[j].check=true
                }
              }
            }
          }else if(flag===2){
            //
            for(let i=0;i<this.simulationData.content.length;i++){
              if(row.id===this.simulationData.content[i].id){
                this.simulationData.content[i].check=false;
                for(let j=0;j<this.simulationData.content[i].lists.length;j++){
                  this.simulationData.content[i].lists[j].check=false
                }
              }
            }
          }else if(flag===3){
            //
            for(let i=0;i<this.simulationData.content.length;i++){
              if(row.id===this.simulationData.content[i].id){
                this.simulationData.content[i].check=false
              }
            }
          }
            /**
             *  
             * @type {Number|number}
             */
            slength=this.simulationData.content.length;
            for(let i=0;i<this.simulationData.content.length;i++){
              if(this.simulationData.content[i].check===true){
              shopArr.push(this.simulationData.content[i].check)
              }
            }
            console.log( shopArr.length)
            if(shopArr.length>=slength){
              this.selectAllFun(1);
            }else {
              this.selectAllFun(3);
            }
            /**
             * 
             */
            this.shopTotalFun()
          },
          /**
           *  
           * 
           * @param row 
           * @param id id
           *
           */
          commodityCheckFun(row,id){// 3
            let arrLengTrue=[];
            let shopActiveLeng='';
            let shopTrue={};
            if(row.check==false){
//              for
              for(let i=0;i<this.simulationData.content.length;i++){
//                
                if(this.simulationData.content[i].id===id){
                  shopActiveLeng=this.simulationData.content[i].lists.length
                  shopTrue=this.simulationData.content[i]
                  for(let j=0;j<this.simulationData.content[i].lists.length;j++){
                    if(Number(row.id)==Number(this.simulationData.content[i].lists[j].id)){
                      this.simulationData.content[i].lists[j].check=true
                    }
                    if(this.simulationData.content[i].lists[j].check===true){
                      arrLengTrue.push(this.simulationData.content[i].lists[j])
                    }
                  }

                }
              }
            }else if(row.check){
              //
              for(let i=0;i<this.simulationData.content.length;i++){
                if(this.simulationData.content[i].id===id){
                  shopActiveLeng=this.simulationData.content[i].lists.length
                  shopTrue=this.simulationData.content[i]
                  for(let j=0;j<this.simulationData.content[i].lists.length;j++){
                    if(Number(row.id)==Number(this.simulationData.content[i].lists[j].id)){
                      this.simulationData.content[i].lists[j].check=false
                    }
                    if(this.simulationData.content[i].lists[j].check===true){
                      arrLengTrue.push(this.simulationData.content[i].lists[j])
                    }
                  }//let j
                }//if end

              }//let i
            }
            //              
            if(arrLengTrue.length>=shopActiveLeng){
              this.shopCheckFun(shopTrue,1);
            }else {
              this.shopCheckFun(shopTrue,3);
            }
            /**
             * 
             */
            this.shopTotalFun()
          },
          /**
           *   X=
           * @param max 
           * @param pri 
           * @param commId Id
           * @param shopId Id
           * @param actNumber  
           */
          commodityPlus(max,pri,commId,shopId,actNumber){
            let shopPrice; //
            let shopCardNumber; //
            let tolPrice=parseInt(pri)*parseInt(actNumber);
            if(parseInt(actNumber)<parseInt(max)){
              shopCardNumber=parseInt(actNumber)+1;
              shopPrice=parseInt(pri)+parseInt(tolPrice);
            }else {
              Toast({
                message:'',
                position: 'top',
                duration: 1000
              });
              shopCardNumber=parseInt(max);
              shopPrice=parseInt(tolPrice);
            }
            for(let i=0;i<this.simulationData.content.length;i++){
              if(Number(shopId)===Number(this.simulationData.content[i].id)){
                for(let j=0;j<this.simulationData.content[i].lists.length;j++){
                  if(Number(commId)==Number(this.simulationData.content[i].lists[j].id)){
                    this.simulationData.content[i].lists[j].Tolprice=shopPrice;
                    this.simulationData.content[i].lists[j].number=shopCardNumber
                  }
                }
              }
            }
            /**
             * 
             */
            this.shopTotalFun()
          },
          /**
           *   X=
           * @param pri 
           * @param commId Id
           * @param shopId Id
           * @param actNumber  
           */
          commodityReduce(pri,commId,shopId,actNumber){
            let shopPrice; //
            let shopCardNumber; //
            let tolPrice=parseInt(pri)*parseInt(actNumber);
            if(parseInt(actNumber)>1){
              shopCardNumber=parseInt(actNumber)-1
              shopPrice=parseInt(tolPrice)-parseInt(pri);
            }else {
              shopCardNumber=1
              shopPrice=parseInt(tolPrice)
            }
            for(let i=0;i<this.simulationData.content.length;i++){
              if(Number(shopId)===Number(this.simulationData.content[i].id)){
                for(let j=0;j<this.simulationData.content[i].lists.length;j++){
                  if(Number(commId)==Number(this.simulationData.content[i].lists[j].id)){
                    this.simulationData.content[i].lists[j].Tolprice=shopPrice;
                    this.simulationData.content[i].lists[j].number=shopCardNumber
                  }
                }
              }
            }
            /**
             * 
             */
            this.shopTotalFun()
          },
          /**
           * 
           */
          shopTotalFun(){
            this.totalPrice=0;
            let totalPrice=0;
            let priceArr=[];
            for(let i=0;i<this.simulationData.content.length;i++){
                for(let j=0;j<this.simulationData.content[i].lists.length;j++){
                  if(this.simulationData.content[i].lists[j].check===true){
                    priceArr.push( this.simulationData.content[i].lists[j].Tolprice)
                  }

              }
            }
            for(let m=0;m<priceArr.length;m++){
              totalPrice +=parseInt(priceArr[m])
            }
            console.log(priceArr)
            console.log(totalPrice)
            this.totalPrice=totalPrice;
          },

          /**
           * 
           * @param e
           */
          touchmoveFun(e){
            let endx=event.touches[0].pageX
            this.leftX= Number(this.startx)-Number(endx)
            if(this.leftX>=50){
              this.leftX=50
            }
            if(this.leftX<=0){
              this.leftX=0
            }
            this.leftX= (Number(this.leftX)*2)/100
            e.currentTarget.style.left='-'+this.leftX+'rem'
          },
          /**
           * div
           * @param e
           */
          touchstartFun(e){
            this.startx = e.touches[0].pageX;
          },
        },
        computed: {

        },
        watch:{

        }
    }
</script>

