/**
 * 
 * @param mobile
 * @returns {*}
 */
export function mobiles(mobile){
  var ipone=/^0{0,1}(13[0-9]|15[1-9]|18[1-9]|17[1-9]|14[1-9])[0-9]{8}$/
  if(mobile==""){
    return ""
  }else if(!ipone.test(mobile)){
    return ""
  }else{
    return true
  }
}
/**
 * 
 * @param scCard
 * @returns {*}
 */
export function scCard(scCard){
// 1518151817X
    var reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
    if(reg.test(scCard) === false) {
      return false;
    }else {
      return true;
    }
  }



